import React, {Fragment ,useEffect} from "react";
import { Route, BrowserRouter,Redirect, Switch } from "react-router-dom";
import CreateAccount from "./pages/CreateAccount";
import Dashboard from "./pages/Dashboard";
import Login from "./pages/Login";
import Plans from "./pages/Plans";
import Investments from "./pages/Investments";
import Portfolio from "./pages/Portfolio";
import {useDispatch,useSelector,} from 'react-redux'
import {getUserProfile} from './store/action/authAction'
const App = () => {
const  dispatch = useDispatch()
  const  {error,userData ,success}= useSelector(state => state?.auth)
useEffect(() => {
  if(localStorage.getItem('token') ){
    if(localStorage.getItem('token').startsWith('Bearer ') ){
      // if(userData){
        getUserProfile(dispatch)
      // }

    }
  }
  else{
  }
},[userData._id])
  const ProtectedRoute = ({component:Component,...rest})=>{
    return (
      <Route {...rest}
      render={(props)=>{
        let l = localStorage.getItem("token")
        console.log(l)
        if(l.startsWith('Bearer')){
        return <Component {...props}/>
        }else{
         return  <Redirect to={{pathname:"/",state:{
            from:props.location
          }}}/>
        }
      }}
      />
    )
  }
  const AdminRoute=({component:Component,...rest})=>{
    return (
      <Route {...rest}
      render={(props)=>{
        if(userData.userType==='Admin'){
        return <Redirect to={{pathname:"/login",state:{
          from:props.location
        }}}/>
        }else{
          return <Component {...props}/>
        }
      }}
      />
    )
  }
  const GuestRoute=({component:Component,...rest})=>{
    return (
      <Route {...rest}
      render={(props)=>{
        if(userData._id){
        return <Redirect to={{pathname:"/dashboard",state:{
          from:props.location
        }}}/>
        }else{
          return <Component {...props}/>
        }
      }}
      />
    )
  }
  return (
    <Fragment>
      {success}
      {error}
      
      <BrowserRouter>
        <Switch>
          <ProtectedRoute exact path="/dashboard" component={Dashboard} />
          <GuestRoute exact path="/create" component={CreateAccount} />
          <ProtectedRoute exact path="/dashboard/plans" component={Plans} />
          <ProtectedRoute exact path="/dashboard/investments" component={Investments} />
          <ProtectedRoute exact path="/dashboard/portfolio" component={Portfolio} />
          <ProtectedRoute exact path="/dashboard/portfolio" component={Portfolio} />
          <GuestRoute exact path="/" component={Login} />
          <Route exact path="*" >
            <h3>no route found</h3>
          </Route>
        </Switch>
      </BrowserRouter>
    </Fragment>
  );
};
export default App;
