import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import {Provider} from 'react-redux'
import store from './store/initStore'

import 'antd/dist/antd.css';

ReactDOM.render(
    // <App />,


    <Provider store={store}>

    <React.StrictMode>
      <App />
    </React.StrictMode>
    </Provider>
    ,
  document.getElementById('root')
);
